.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.MuiPaper-root.MuiCard-root.makeStyles-root-1.MuiPaper-elevation1.MuiPaper-rounded {
  width: 40%;
  margin: 25px auto;
  text-align: center;
}

h2.MuiTypography-root.Total.MuiTypography-h5 {
  color: grey;
  font-weight: bold;
}

h2.MuiTypography-root.Deaths.MuiTypography-h5 {
  color: red;
  font-weight: bold;
}

h2.MuiTypography-root.Recovered.MuiTypography-h5 {
  color: green;
  font-weight: bold;
}

.MuiTableCell-stickyHeader{
  font-weight: bold;
}

.Cases h2,.Death h2, .Recovery h2{
  font-weight: bold;
}

.Cases h2{
  color: grey;
}

.Death h2{
  color: red;
}

.Recovery h2{
  color: green;
}

.Summary {
  width: 50%;
  margin: 0 auto;
  text-align: center;
  float: left;
}

.MuiAutocomplete-root.MuiAutocomplete-hasClearIcon.MuiAutocomplete-hasPopupIcon {
  float: right;
  margin: 10px;
}

.Container {
  color: white;
}

div#root {
  background: url(./backgroundimage.PNG);
  background-color: #cccccc; /* Used if the image is unavailable */
  
  background-position: center; /* Center the image */
  background-repeat: repeat; /* Do not repeat the image */
  

}

.SummaryContianer {
  width: 100%;
  display: inline-block;
}

.summary2 {
  width: 50%;
  float: left;
}

.SummaryDilaog {
  width: 100%;
  display: inline-block;
}

.casescummary {
  float: left;
  width: 50%;
}

.deathssummary {
  float: left;
  width: 50%;
  margin-left: 0px;
}

@media only screen and (max-width: 600px) {
  .Summary,
  .summary2{
    width: 100%;
  }
}